import React from 'react'
import { NavLink } from 'react-router-dom'
import Data_Fetch_Api from '../contexts/Data_Fetch_Api';
import Loader from './Loader';

function MainHeader() {
    const [IsLoading, Error] = Data_Fetch_Api('/api/auth/admin/data');
    if (IsLoading) {
        return <Loader message="Loading.." />
    }
    return (
        <>
            <div className=' w-[100%] h-[60px] z-40 sticky top-0 bg-[#0c0f1e] border-b-[1px] border-[#2b3148] flex justify-center items-center'>
                <div className='w-[24%] h-[auto] flex justify-center items-center  '>

                    <NavLink to='/' className='flex justify-center items-center' >
                        <div className=' w-[70px] h-[auto] flex justify-center items-center'>
                            <img src="https://imgs.search.brave.com/Er0gh71OnmWHzlTaxGe7vxoKS5RU5qM9vxgYD4-ef20/rs:fit:500:0:0/g:ce/aHR0cHM6Ly9zZWVr/bG9nby5jb20vaW1h/Z2VzL1QvdG9reXUt/dGVjaG5vLXN5c3Rl/bS1sb2dvLTgyRkM0/QTcxQzgtc2Vla2xv/Z28uY29tLnBuZw" className='w-[50px] h-[40px]' alt="Loading.." />

                        </div>
                        <h1 className=' text-[30px] font-[700] text-[#fff]'>TECHKRITI</h1>
                    </NavLink>

                </div>
                <nav className='w-[76%] h-[100%] flex justify-center items-center'>
                    <ul className='w-[100%] h-[80%] flex justify-center items-center gap-8'>
                        <NavLink to={`/government-engineering-college-siwan/events/team-list/2024-25`} className='text-[#fff] text-[14px] transition-all ease-in-out delay-200 font-[600]  hover:text-[red]'>
                            <li> TEAM 2024-25</li>
                        </NavLink>
                        <NavLink to='/government-engineering-college-siwan/total/events/list' className='text-[#fff] text-[14px] transition-all ease-in-out delay-200 font-[600]  hover:text-[red]'>
                            <li>EVENTS</li>
                        </NavLink>
                        <NavLink to="/government-engineering-college-siwan/total/clubs/list" className='text-[#fff] text-[14px] transition-all ease-in-out delay-200 font-[600]  hover:text-[red]'>
                            <li> CLUBS</li>
                        </NavLink>
                        <NavLink to='/government-engineering-college-siwan/contest/lists' className='text-[#fff] text-[14px] transition-all ease-in-out delay-200 font-[600]  hover:text-[red]'>
                            <li>CONTEST</li>
                        </NavLink>
                        <NavLink to="/government-engineering-college-siwan/technical-club/total/member/list" className='text-[#fff] text-[14px] transition-all ease-in-out delay-200 font-[600]  hover:text-[red]'>
                            <li> CLUB MEMBERS</li>
                        </NavLink>
                        <NavLink to="/government-engineering-college-siwan/event/gallery/" className='text-[#fff] text-[14px] transition-all ease-in-out delay-200 font-[600]  hover:text-[red]'>
                            <li>EVENTS GALLERY</li>
                        </NavLink>
                        <NavLink to='/government-engineering-college-siwan/technical-club/contacts' className='text-[#fff] text-[14px] transition-all ease-in-out delay-200 font-[600]  hover:text-[red]'>
                            <li> CONTACT US</li>
                        </NavLink>
                        {
                            Error ? (
                                <NavLink to='/admin/dashboard/login' className='text-[#fff] text-[14px] transition-all ease-in-out delay-200 font-[600]  hover:text-[red]'>
                                    <li> ADMIN LOGIN</li>
                                </NavLink>
                            ) : (
                                <NavLink to='/admin/dashboard/' className='text-[#fff] text-[14px] transition-all ease-in-out delay-200 font-[600]  hover:text-[red]'>
                                    <li> Dashboard</li>
                                </NavLink>
                            )
                        }

                    </ul>
                </nav>
            </div>

        </>
    )
}

export default MainHeader
