import React from 'react'
import Data_Fetch_Api from '../contexts/Data_Fetch_Api'
import AnswerWritPageLoader from '../partials/AnswerWritPageLoader';

function McqSolvePage() {
    const [IsLoading, Error, data] = Data_Fetch_Api('/api/contest/question-mcq');
    if (Error) {
        return (<>
            <div>
                <h1 className=' text-center font-[700] text-[26px] w-[500px]'>This time sum technical issue please reload page</h1>
            </div>
        </>
        )
    }
    if (IsLoading) {
        return <AnswerWritPageLoader message="Loading.." />
    }
    return (
        <>
            <div className='w-[100%] text-[#fffc] h-[100%] bg-[#090e16] flex justify-end items-center'>
                <div className='w-[96%]  h-[100%] overflow-auto space-y-10 pt-[20px] pb-[20px] '>
                    {
                        data.map((element, index) => (
                            <>
                                <div key={index} className=' w-[98%] h-[auto] rounded-[10px] shadow-inner border-[1px] border-[#243152] bg-[#0a131c] p-[10px] pt-[16px] pl-[26px] pb-[26px]'>
                                    <h1 className=' text-[#ffffffdc] w-[98%] h-[auto] text-[18px] font-[700]'>{index + 1}<span className='ml-[10px] text-[16px] font-[600]'>{element.questionname}</span><span className="font-[500] text-[17px] material-symbols-outlined">
                                        question_mark
                                    </span></h1>
                                    <div className='w-[98%] cursor-pointer h-[45px] border-[1px] border-[#192f41] mt-4 rounded-[5px] p-[10px] pl-[20px] flex items-center justify-start gap-2'>
                                        <label htmlFor={`option1${[index]}`} className=' w-[100%] h-[45px] cursor-pointer flex items-center justify-start gap-2'>
                                            <input type='radio' name='answer' id={`option1${[index]}`} /><p>{element.option1}</p>
                                        </label>
                                    </div>
                                    <div className='w-[98%] cursor-pointer h-[45px] border-[1px] border-[#192f41] mt-4 rounded-[5px] p-[10px] pl-[20px] flex items-center justify-start gap-2'>
                                        <label htmlFor={`option2${[index]}`} className=' w-[100%] h-[45px] cursor-pointer flex items-center justify-start gap-2'>
                                            <input type='radio' name='answer' id={`option2${[index]}`} /><p>{element.option2}</p>
                                        </label>
                                    </div>
                                    <div className='w-[98%] cursor-pointer h-[45px] border-[1px] border-[#192f41] mt-4 rounded-[5px] p-[10px] pl-[20px] flex items-center justify-start gap-2'>
                                        <label htmlFor={`option3${[index]}`} className=' w-[100%] h-[45px] cursor-pointer flex items-center justify-start gap-2'>
                                            <input type='radio' name='answer' id={`option3${[index]}`} /><p>{element.option3}</p>
                                        </label>
                                    </div>
                                    <div className='w-[98%] cursor-pointer h-[45px] border-[1px] border-[#192f41] mt-4 rounded-[5px] p-[10px] pl-[20px] flex items-center justify-start gap-2'>
                                        <label htmlFor={`option4${[index]}`} className=' w-[100%] h-[45px] cursor-pointer flex items-center justify-start gap-2'>
                                            <input type='radio' name='answer' id={`option4${[index]}`} /><p>{element.option4}</p>
                                        </label>
                                    </div>


                                </div>
                            </>
                        ))
                    }
                    <div className='w-[100%] h-[60px] flex justify-center items-center gap-6'>

                        <span class="material-symbols-outlined cursor-pointer">
                            arrow_back_ios
                        </span>
                        <button className='text-[13px] border-[1px] border-[#1a2640e7] font-[700] w-[100px] h-[40px] rounded-[5px] bg-[#122739ae]'> Submit</button>
                        <span class="material-symbols-outlined cursor-pointer">
                            arrow_forward_ios
                        </span>
                    </div>
                </div>
            </div>
        </>
    )
}

export default McqSolvePage
